@import "../mixins";
.footer{
    padding-top: 49px;
    background-color: var(--color-black);
    padding-bottom: 40px;
    &__item{
        display: flex;
        justify-content: space-between;
        @include mobile-super{
            display: flex;
            flex-direction: column-reverse;
        }
    }
    &__text{
        color: #2D3746;
        font: var(--text-title-4);
        @include mobile-super{}
        margin-bottom: 15px;
        font-size: 20px;
    }
}