@import "../mixins";
.comfort{
    padding-top: 145px;
    background-color: var(--color-bg-2);
    @include mobile{
        padding-top: 0;
    }
    &__title{
        margin-bottom: 22px;
    }
    &__text{
        margin-bottom: 49px;
    }
    &__item{
        width: 100%;

        img{
            @include desktop{
                margin-top: 40px;
            }
        }
    }
    &__items{
        display: flex;
        @include desktop{
            display: block;
        }
    }

}

.comfort-btn{
    background-color: var(--color-btn-2);
    color: var(--color-price);
    font: var(--text-4);
    padding: 12px 39px;
    border-radius: 10px;
    width: 262px;
    height: 49px;
}

