@mixin desktop {
  @media (max-width: (1110px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin tablet-1 {
  @media (max-width: (980px)) {
    @content;
  }
}
@mixin tablet-2 {
  @media (max-width: (800px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (768px)) {
    @content;
  }
}
@mixin mobile-super {
  @media (max-width: (460px)) {
    @content;
  }
}
