@import "../mixins";
.link{
    background-color: var(--color-bg-2);
    padding-top: 162px;
    @include mobile{
        padding-top: 0;
    }
    &__items{
        display: flex;
        text-align: center;
        padding-bottom: 163px;
        @include tablet-2{
           display: flex;
           flex-direction: column-reverse;
           align-items:center;
        }
    }
    &__descr{
        margin-bottom: 65px;
        margin-top: 22px;
        width: 72%;
        @include mobile{
            width: 100%;
        }
    }
    &__item{
        border-radius: 15px;
        background-color: var(--color-card);
        width: 317px;
        height: 340px;
        padding-top: 30px;
        margin-right: 26px;
        @include mobile{
            margin-bottom: 51px;
            width: 314px;
        }
    }
    &__text{
        color: var(--color-test);
        padding: 0;
        margin: 0;
        font: var(--text-title-1);
    }
    &__title{
        color: var(--color-white);
        padding: 0;
        margin: 0;
        padding-top: 28px;
        padding-bottom: 18px;
        font: var(--text-title);
    }
}