@import "../mixins";
.card{
    background-color: var(--color-bg-2);
    padding-top: 112px;
    padding-bottom: 169px;
    &__title{
        margin-bottom: 22px;
    }
    &__text{
        margin-bottom: 44px;
    }
    &__items{
        display: flex;
        @include tablet{
            flex-direction: column;
            align-items: center;
        }
    }
    &__item{
        text-align: center;
        background: #273140;
        box-shadow: 0px 1px 26px rgba(24, 31, 41, 0.15);
        border-radius: 15px;
        width: 317px;
        height: 450px;
        margin-right: 105px;
        padding: 20px 41px;
        @include desktop{
            margin-right: 20px;
        }
        @include tablet{
            margin-bottom: 30px;
        }
        
        strong{
            color: var(--color-test);
            font: var(--text-title-4);

        }
    }
    &__descr{
        color: var(--color-white);
        font: var(--text-title);
        padding: 0;
        margin: 0;
        padding-top: 18px;
        padding-bottom: 5px;
    }
    &__info{
        padding-bottom: 15px;
        color: var(--color-text);
        margin: 0;
    }


}

.btn__card-1{
    width: 248px;
    height: 38px;
    background: var(--color-price);
    border-radius: 5px;
    font: var(--text-title-3);
    color: var(--color-white);
    margin-top: 15px;
    
}
