@import "../mixins";
.social{
    padding-top: 145px;
    background-color: var(--color-bg-2);
    padding-bottom: 113px;
    @include mobile{
        padding-top: 0;
    }
    &__title{
        margin-bottom: 22px;
    }
    &__text{
        margin-bottom: 22px;
        width: 60%;
        @include mobile{
            width: 100%;
        }
    }
    &__items{
        @include tablet{
            display: grid;
            grid-template-columns: 101px 98px;
        }
    }
    &__item{
        margin-right: 38px;

        img{
            width: 63px;
            height: 63px;
        }
    }
}