@import "../mixins";
.nav{
    @include desktop{
        display: flex;
        justify-content: center;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 100;
        transform: translateY(100%);
        transition: transform 250ms cubic-bezier(0.5, 0, 0.5, 1);
        height: 100vh;
    }
    &__list{
        display: flex;
        align-items: center;
        @include desktop{
            list-style: none;
            display: flex;
            height: 100%;
            flex-direction: column;
            justify-content: space-evenly;
            text-align: center;
            margin: 0;
            background-color: #111;
            width: 100%;
        }
    }
    &__item{
        margin: 0 57px;
    }
    &__link{
        color: var(--color-text);
        font: var(--text);
        &:hover{
            color: var(--color-btn);
            transition: all .2s ease-in-out;
        }

        &--active{
            color: var(--color-btn);
        }
    }
}

@include desktop{
    .nav-open {
        overflow: hidden;
    }

    .nav-open .nav {
        transform: translateX(0);
        width: 100vw;
    }

    .nav-open .logo {
        display: none;
    }

    .nav-open .hamburger {
        transform: rotate(0.625turn);
        background-color:var(--color-white);
    }

    .nav-open .hamburger::before {
        transform: rotate(90deg) translateX(-6px);
        background-color:var(--color-white);
    }

    .nav-open .hamburger::after {
        opacity: 0;
    }

    .hamburger {
        display: block;
        position: relative;
    }

    .hamburger,
    .hamburger::before,
    .hamburger::after {
        background-color:var(--color-white);
        width: 2em;
        height: 3px;
        border-radius: 1em;
        transition: transform 250ms ease-in-out;
    }

    .hamburger::before,
    .hamburger::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
    }

    .hamburger::before {
        top: 6px;
    }

    .hamburger::after {
        bottom: 6px;
    }

    .nav__item {
        color: #fff;
    }

    .nav__link::before {
        content: "";
        display: block;
        position: absolute;
        bottom: 3px;
        left: 0;
        height: 3px;
        width: 100%;
        background-color: #fff;
        transform-origin: right top;
        transform: scale(0, 1);
        transition: color 0.1s, transform 0.2s ease-out;
    }

    .nav__link:active::before {
        background-color: #fff;
    }

    .nav__link:hover::before,
    .nav__link:focus::before {
        transform-origin: left top;
        transform: scale(1, 1);
    }
}
