@import "../mixins";
.header{
    padding-top: 56px;
    background-color: var(--color-bg-2);
    position: fixed;
    z-index: 111;
    top: 0;
    left: 0;
    right: 0;
        img{
            max-width: none;
        }
        &__logo{
            @include desktop{
                display: none;
            }
        }
    }

    .nav-toggle{
        display: none;
        @include desktop {
                display: block;
                padding: 1.3em;
                background: transparent;
                border: 0;
                cursor: pointer;
                position: absolute;
                right: -1em;
                top: 4em;
                z-index: 1000;
    }
 }
    
    
