@import "../mixins";
.hero{
    padding-top: 210px;
    background-color: var(--color-bg-2);
  @include mobile{
      padding-top: 103px;
  }
    &__items{
        display: flex;
    }
    &__title{
        color: var(--color-white);
        font: var(--title);
        padding: 0;
        margin: 0;
        margin-bottom: 38px;
        @include tablet{
            font-size: 50px;
        }
        @include mobile{
            font-size: 35px;
        }
    }
    &__text{
        color: var(--color-text);
        font: var(--text-2);
        padding: 0;
        margin: 0;
        margin-bottom: 78px;
    }
    &__img{
        img{
            max-width: none;
            @include tablet-1{
                display: none;
            }
        }
    }
    &__btn{
        padding: 12px 39px;
        background-color: var(--color-btn);
        border-radius: 10px;
        width: 262px;
        height: 49px;
        color: var(--color-white);
        font: var(--text-3);
        margin-bottom: 190px;
        white-space: nowrap;
    }
    &__social{
        margin-top: 190px;
        padding-bottom: 175px;
        display: flex;
        flex-direction: column;
        @include mobile{
            margin-top: 150px;
        }
    }
    &__phone{
        color: var(--color-text);
        font: var(--text);
        margin-right: 79px;
        position: relative;
        &:hover{
            color: var(--color-price);
            transition: all .2s ease-in-out;
        }
        &::after{
            position: absolute;
            content: '';
            background-image: url(../img/phone.svg);
            top: -5px;
            left: -61px;
            width: 42px;
            height: 34px;
        }
    }
    &__mail{
        color: var(--color-text);
        font: var(--text);
        position: relative;
        &:hover{
            color: var(--color-price);
            transition: all .2s ease-in-out;
        }
        &::after{
            position: absolute;
            content: '';
            background-image: url(../img/mail.svg);
            top: -5px;
            left: -61px;
            width: 40px;
            height: 34px;
        }
    }
}